.profile {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 10px;
    box-shadow: 0px 0px 3px 1px rgba(185, 185, 185, 1);
    width: 250px;
    height: 330px;
    overflow: hidden;
    border-radius: 4px;
  }
  
  .description {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    padding: 30px 0;
  }
  
  .avatar {
    width: auto;
    height: 100px;
    border-radius: 50%;
    padding-bottom: 30px;
  }
  
  .name {
    font-size: 25px;
    font-weight: 700;
  }
  
  .name,
  .tag {
    padding-bottom: 10px;
  }
  
  .tag,
  .location {
    font-weight: 600;
    font-size: 14px;
    color: rgb(151, 163, 175);
  }
  
  .stats {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }
  
  .item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 0.5px solid rgb(151, 163, 175);
    width: 100%;
    padding: 15px 0;
    background-color: rgb(240, 244, 247);
  }
  
  .label {
    padding-bottom: 2px;
    font-size: 11px;
    color: rgb(151, 163, 175);
    font-weight: 600;
  }
  
  .quantity {
    font-size: 12px;
    font-weight: 700;
  }